/*  Mega Menu CSS - RVP  */

.nav-link {
  /* font-size: 0.8rem; */
  margin: 0;
  padding: 0 4px !important;
  color: #163269 !important;
  font-size: 0.9rem;
}

.nav-link:hover {
  color: #3db166 !important;
  font-weight: 600;
}

#seeMoreLink {
  color: #607d8b;
}

#seeMoreLink:hover {
  color: black;
  font-weight: 400;
}

.dropdown-menu {
  padding: 1em;

  /* height: 60vh; */
  /* width: 60vw; */
  background-color: #ffffff;

  padding: 0 !important;

  /* right: 0;
  left: auto !important; */

  /* outline: 1px solid red; */
  box-shadow: 4px 2px 20px 3px rgb(0 0 0 / 20%);
  transition: all 1s ease;
  font-size: 0.8rem;
}
/* .dropdown-menu .show {
  
} */

.dropdown-item:hover {
  outline: none;
  box-shadow: none;
  background: none;
}
.dropdown-item a:hover {
  color: black;
}

.dropdown-item {
  height: 2em;
  /* padding-left: 1em; */
}

.dropdown-header {
  font-weight: 600;
  padding-left: 0em;
  padding-bottom: 0.8rem;
  color: black;

  text-decoration: underline;
  text-underline-offset: 0.5rem;
  /* text-decoration-color: rgb(0, 102, 179); */
  text-decoration-color: #3db166;
  /* text-decoration-color: coral; */
  /* text-decoration-color: rgb(198, 66, 110); */
  /* text-decoration-color: linear-gradient(
    to right,
    rgb(100, 43, 115) 0%,
    rgb(198, 66, 110) 51%,
    rgb(100, 43, 115) 100%
  ) !important; */
  text-decoration-thickness: 0.1rem;
}
