/* new flex menu starts rvp */
.flex-container {
  display: flex;

  /* background-color: DodgerBlue; */
  flex-wrap: nowrap;
  flex-direction: row;
  /* justify-content: space-between; */
  /* width: 100%; */
}

.flex-container > div {
  margin: 0.5rem;
  padding: 0.8rem;
  word-wrap: break-word;
  width: 15vw;
  /* outline: 10px solid red; */

  /* background-color: aquamarine; */
  /* outline: 1px solid red; */
}

/* for mobile view */

@media (max-width: 800px) {
  .flex-container {
    flex-direction: column;
    margin: 0.2rem;
  }
  .dropdown-menu {
    height: 50vh;
    overflow-y: scroll;
    overflow-x: auto;
    /* margin-left: 0.5rem; */
    margin: 0.3rem;
    width: 80vw;
    padding: 0;
  }
  .flex-container > div {
    width: 45vw;
    margin-top: 0rem;
    /* outline: 1px solid blue; */
  }
}

/*  Mega Menu CSS END - RVP  */
